import { CangjieInputEvent, Controller, environment } from '@ali/4ever-cangjie';
import { createSpaceHandler, Delimiter } from '@ali/4ever-utils';

export default function createOnCangjieInput(
  mark,
  delimiter: Delimiter | Delimiter[],
  insideWhitespace: boolean,
  alwaysTrailSpace = false,
) {
  let onSpace;
  if (delimiter) {
    onSpace = createSpaceHandler({
      mark,
      delimiter,
      insideWhitespace,
      alwaysTrailSpace,
    });
  }
  return function onCangjieInput(
    event: CangjieInputEvent,
    controller: Controller,
    next: Function,
  ) {
    if (!onSpace || event.detail.data !== ' ')
      return next();
    return onSpace(event, controller, next);
  };
}
